@import url('https://fonts.googleapis.com/css2?family=Creepster&family=Source+Sans+Pro:wght@400;600;700&display=swap');

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.game-menu {
  /* position: relative; */
  /* align-items: center; */
  /* align-content: center; */
  height: 200px;
  width: 600px;
  margin-bottom: 10px;
  border-radius: 10px;
  background-color: #555;
}

.game-title {
  font-family: 'Creepster';
  color: red;
  box-shadow: 100px;
  box-sizing: border-box;
  text-shadow: 100px;
}

.textfield {
  margin-top: 28%;
  height: 20px;
  width:  300px;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
